

































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class RoomTitle extends Vue {
  private numChinese: string[] = ['', '', '', '', '', '', '', '', ''];
  @Prop({default: {}}) private detailInfo!: any;

  get roomTypeInfo(): string {
    // b
    // nnnn
    const roomCountDetail: string = (this.detailInfo.bedCount === 0 ? '' : `${this.detailInfo.bedCount}`) +
                                    (this.detailInfo.hallCount === 0 ? '' : `${this.detailInfo.hallCount}`) +
                                    (this.detailInfo.kitchenCount === 0 ? '' : `${this.detailInfo.kitchenCount}`) +
                                    (this.detailInfo.bathroomCount === 0 ? '' : `${this.detailInfo.bathroomCount}`);
    // 
    const roomSquare: string = this.detailInfo.biz
                               ? ` | ${this.detailInfo.bedSquareMeter}/${this.detailInfo.roomSquareMeter} ` : '';
    // 
    const roomFloor: string = this.detailInfo.biz
                              ? ` | ${this.detailInfo.floor}/${this.detailInfo.totalFloor}` : '';
    // 
    const roomElevator: string = this.detailInfo.elevator === 1
                                 ? ' | ' : (this.detailInfo.elevator === 2 ? ' | ' : '');

    return `${roomCountDetail}${roomSquare}${roomFloor}${roomElevator}`;
  }

  private gotoDescription(): void {
    this.$router.push({name: 'descriptionRoom'});
  }
}
